import React from 'react';
import { Button, Divider} from 'semantic-ui-react'
import ReactDOM from 'react-dom';
import { Redirect } from 'react-router-dom'


class Survey extends React.Component {
	state = {}

    handleChange = (e, { value }) => this.setState({ value })

    state = {
    	redirect: false
    }
  	setRedirect = () => {
	    this.setState({
	      redirect: true
	    })
  	}
    renderRedirect = () => {
        if (this.state.redirect) {
          return <Redirect to='/home' />
        }
    }

	render() {
		const { value } = this.state
	  	return <div id="survey-container">
	  		<iframe src="https://docs.google.com/forms/d/e/1FAIpQLSeP7ROrJrvFl880EgDp1cNXm0xcXQ0CYUHfpRvI2trAoCfUMQ/viewform?embedded=true" width="100%" height="100%" frameborder="0" marginheight="0" marginwidth="0">Loading…</iframe>
			{this.renderRedirect()}
			<Divider hidden />
			<Button basic content='Go to homepage' onClick={this.setRedirect} />
		</div>;
	}
}

export default Survey;

