import React from 'react';
import { Button, Checkbox, Form, Segment, Label, Divider, Input } from 'semantic-ui-react'
import ReactDOM from 'react-dom';
import Budget from './Budget';
import CalendarStart from './CalendarStart';
import CalendarEnd from './CalendarEnd';
import Preferences from './Preferences';
import { Redirect } from 'react-router-dom'


const options = [
  { key: 'm', text: 'Male', value: 'male' },
  { key: 'f', text: 'Female', value: 'female' },
  { key: 'o', text: 'Other', value: 'other' },
  { key: 'n', text: 'No preference', value: 'none' },
]

class Survey extends React.Component {
	state = {}

    handleChange = (e, { value }) => this.setState({ value })

    state = {
    	redirect: false
    }
  	setRedirect = () => {
	    this.setState({
	      redirect: true
	    })
  	}
    renderRedirect = () => {
        if (this.state.redirect) {
          return <Redirect to='/home' />
        }
    }

	render() {
		const { value } = this.state
	  	return <div id="survey-container">

	  		<Segment padded>
				<Label attached='top left' size='large'>What are you looking for?</Label>
				<Form>
					<Form.Field 
						label="I have a place and I'm looking for roommate(s)! (Skip the apartment section of the survey)"  
						control='input' 
						type='checkbox' 
					/>
      				<Form.Field 
      					label="I don't have a place, but I would like to live with cool people." 
      					control='input' 
      					type='checkbox' 
      				/>
      				<Form.Field 
      					label="I'm interested in additional services such as storage, moving, furniture, etc" 
      					control='input' 
      					type='checkbox' 
      				/> 
				</Form>
			</Segment>

	  		<Segment padded>
				<Label attached='top left' size='large'>Apartment Search Information</Label>
				<Form>
					<Divider hidden />
					<Budget />
					<CalendarStart />
					<CalendarEnd />
					<Divider hidden />
					
				    <Form.Field inline>
				      <label >I am looking for an apartment in</label>
				      <Input
					    icon='search'
					    iconPosition='left'
					    placeholder='Location...'
					  />
				    </Form.Field>
				</Form>
			</Segment>

	  		<Segment>
	  			<Label attached='top left' size='large'>Roommate Search Information</Label>
	  			<Form>
			    	<Segment padded basic>
			    		<Label size='large' basic>
			    			How orderly are you? <Checkbox label='Hard sell' />
			    		</Label>
			    		<Divider hidden />
			    		<Preferences 
			    			textLabel={"1 = I'm ok with clothes on the floor, 5 = Everything has to be in its place"}
			    		/>
			    	</Segment>
			    	<Segment padded basic>
			    		<Label size='large' basic>
			    			How much noise can you tolerate at night? <Checkbox label='Hard sell' />
			    		</Label>
			    		<Divider hidden />
			    		<Preferences 
			    			textLabel={"1 = I can sleep through anything. 5 = I'm a light sleeper."}
			    		/>
			    	</Segment>
			    	<Segment padded basic>
			    		<Label size='large' basic>
			    			How much do you want to socialize with your roommates? <Checkbox label='Hard sell' />
			    		</Label>
			    		<Divider hidden />
			    		<Preferences 
			    			textLabel={"1 = I prefer to keep to myself. 5 = I want to hang out with my roommates."}
			    		/>
			    	</Segment>

			    	<Form.Select
			            fluid
			            label='Preferred gender of future roommates (s)'
			            options={options}
			            placeholder='Gender'
		          	/>

			         <Form.Field>
				      <label>Do you have any allergies that your future roommates should know about?</label>
				      <input placeholder='Description' />
				    </Form.Field>

				    <Form.Field>
				      <label>Do you have any accessibility requirements that your future roommates should know about?</label>
				      <input placeholder='Description' />
				    </Form.Field>

				    <Form.Field>
				      <label>How often will you have guests over.</label>
				      <input placeholder='Description' />
				    </Form.Field>

				    <Form.Field>
				    	<label>Do you have any pets? Are you ok with living with a pet?</label>
				      <input placeholder='Description' />
				    </Form.Field>

				    <Form.Field>
				      <label>Any additional requirements?</label>
				      <input placeholder='Description' />
				    </Form.Field>
				    
				    <Form.Field>
				      <label>Let future roommates know a little more about yourself. (Occupation, etc)</label>
				      <input placeholder='Description' />
				    </Form.Field>
			  </Form>
			</Segment>
			{this.renderRedirect()}
			<Button onClick={this.setRedirect}> Submit</Button>
		</div>;
	}
}

export default Survey;