const RoommateGrid = () => {


  const col = neighborhoods.map(neighborhood => {
    return(
        <NeighborhoodCard
          neighborhood={neighborhood}
        />
      
    );
  });

  return (
    <div id="search-container">
        <Input fluid icon='search' placeholder='Search by company, area, etc' />
        <Segment>
        <Item.Group divided>
            {col}
        </Item.Group>
        </Segment>
    </div>
  );

};

