import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
 
import Home from './components/Home';
import Error from './components/Error';
import Survey from './components/Survey';
import HousingFormWrapper from './components/HousingFormWrapper';
import RoommateSearch from './components/RoommateSearch';
import RoommateGrid from './components/RoommateGrid';
import Tabs from './components/Tabs';
import {  Header,Image } from 'semantic-ui-react'


import "./App.css"
 
class App extends Component {
  render() {
    return (      
       <BrowserRouter>
        <div>
            <Switch>
              <Route path="/migreat" component={Home} exact/>
              <Route path="/survey" component={HousingFormWrapper}/>
              <Route path="/survey2" component={Survey}/>
              <Route path="/roommategrid" component={RoommateGrid}/>
              <Route path="/home" component={Tabs}/>
              <Route component={Error}/>
           </Switch>
        </div> 
      </BrowserRouter>
    );
  }
}
 
export default App;