import React from 'react'
import { Button, Icon, Image, Item, Label } from 'semantic-ui-react'


const Place = ( {place}) => {
  return (
    <Item>
      <Item.Image src={place.src} />

      <Item.Content>
        <Item.Header as='a'>{place.name}</Item.Header>
        <Item.Meta>
          <span className='cinema'>{place.address}</span>
        </Item.Meta>
        <Item.Description>{place.description}</Item.Description>
        <Item.Extra>
          <Label>Studio - 2 Bedroom</Label>
          <Label>{place.extra1}</Label>
          <Label>{place.extra2}</Label>
        </Item.Extra>
      </Item.Content>
    </Item>
  );
};

export default Place;